import { SweetAlertService } from './../../../core/services/alert-message/sweet-alert.service';
import { ENDPOINTS } from '@shared';
import { ApiService } from './../../../core/services/api.service';
import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-form-list',
  templateUrl: './form-list.component.html',
  styleUrls: ['./form-list.component.scss']
})
export class FormListComponent implements OnInit, AfterViewInit {
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<Object>;
  @Input() dataSource: MatTableDataSource<Object>;
  @Input() displayedColumns = '';
  @Output() selectInListFunction: EventEmitter<any> = new EventEmitter();
  @Output() deleteInListFunction: EventEmitter<any> = new EventEmitter();
  @Output() updateListFunction: EventEmitter<any> = new EventEmitter();
  @Output() idForUpdate: number = 0;
  @Output() idForDelete: number = 0;
  @Output() elementObject: any;
  @Output() getVoucherRef: EventEmitter<any> = new EventEmitter();
  @ViewChild('myTemplate') customModal: TemplateRef<any>;
  @ViewChild('signatoryEdit') signatoryEditModal: TemplateRef<any>;
  signatory: any = {};
  signatoryRef: any = {};

  constructor(public dialog: MatDialog, private api: ApiService, 
    private spinner: NgxSpinnerService, private alert: SweetAlertService) { }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    if(this.dataSource){
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  getObject(id) {
    this.idForUpdate = id;
    this.selectInListFunction.emit();
  }

  deleteObject(id, obj: any = '') {
    this.idForDelete = id;
    this.elementObject = obj;
    this.deleteInListFunction.emit(obj);
  }

  openEditModal(id, obj: any) {
    this.signatory = JSON.parse(JSON.stringify(obj));
    this.signatoryRef = obj;
    this.dialog.open(this.signatoryEditModal, {
      disableClose: true,
      width: '350px',
      data: obj
    });
  }

  updateObject(): void {
    if (this.signatoryRef.id === undefined) {
      let data = this.dataSource.data;
      data.splice(this.dataSource.data.findIndex(r => r === this.signatoryRef), 1, this.signatory);
      this.updateListFunction.emit(data);
    } else {
      this.updateListFunction.emit(this.signatory);
    }
    this.dialog.closeAll();
  }

  closeModal() {
    this.dialog.closeAll();
  }
  viewHistory(data){
    console.log('data', data);
    // this.spinner.show()
    


    this.api.findByParam(ENDPOINTS.voucherAttachment, data?.voucherAttachmentReference).subscribe(res =>{
      if(!res?.responseData?.data?.fileURL) {
        this.alert.customErrorMessage("No Available file")
      }
      
      window.open(res?.responseData?.data?.fileURL);
    }, error =>{
      this.alert.customErrorMessage("No Available File");
    })
    
  }


}
