import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { APP_DECISION_STATUS, ENDPOINTS, SUBMISSION_TYPE } from '@shared';
import { ApplicationS2RationaleList } from 'app/core/models/application-rationale-list-s2.model';
import { ApplicationRationaleList } from 'app/core/models/application-rationale-list.model';
import { RationaleS2 } from 'app/core/models/rationale-s2.model';
import { Rationale } from 'app/core/models/rationale.model';
import { TypeOfRational } from 'app/core/models/type-of-rational.model';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-incomplete-disapproved-rationale',
  templateUrl: './incomplete-disapproved-rationale.component.html',
  styleUrls: ['./incomplete-disapproved-rationale.component.scss']
})
export class IncompleteDisapprovedRationaleComponent implements OnInit {

  rationale = new Rationale();
  rationaleS2 = new RationaleS2();
  typeOfRational = new TypeOfRational();
  applicationRationaleList = new ApplicationRationaleList();
  applicationRationaleListS2 = new ApplicationS2RationaleList();
  rationaleList = [];
  selectedRationaleList = [];

  submissionType = '';
  isOther = false;
  otherDescription = '';
  currentUser: any;

  constructor(
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private router: Router,
    private dataStorage: DataStorageService,
    private spinner: NgxSpinnerService,
    public dialogRef: MatDialogRef<IncompleteDisapprovedRationaleComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {

    if (data.applicationStage === 's1') {
      this.submissionType = data.s1Application.submissionType;
    } else {
      this.submissionType = data.s2Application.submissionType;
    }

  }

  ngOnInit(): void {
    this.getRationale();
    this.currentUser = this.dataStorage.getUserAccount();
    console.log('ksahkjakj', this.data);

  }



  getRationale(): void {
    let endpoint
    let submissionTypeForm

    if (this.data.applicationStage === 's1') {
      submissionTypeForm = this.data.s1Application.submissionType;
      this.rationale.applicationForm = this.data.s1Application;
    } else {
      submissionTypeForm = this.data.s2Application.submissionType;
      this.rationale.applicationFormS2 = this.data.s2Application;
    }

    if (submissionTypeForm === SUBMISSION_TYPE.ORIGINAL) {
      endpoint = ENDPOINTS.typeOfRationale;
    } else if (submissionTypeForm === SUBMISSION_TYPE.REVISION) {
      endpoint = ENDPOINTS.typeOfRationale + '/category/FOR_COMPLIANCE';
    } else if (submissionTypeForm === SUBMISSION_TYPE.COMPLIANCE ||
      this.data.s1Application.decisionStatus === APP_DECISION_STATUS.INCOMPLETE) {
      endpoint = ENDPOINTS.typeOfRationale + '/category/FOR_INCOMPLETE_DECISION';
    } else if (this.data.s1Application.hasToa) {
      endpoint = ENDPOINTS.typeOfRationale + '/category/FOR_TOA_APPLICATION';
    }

    this.apiService.findAll(endpoint).subscribe(
      (res: any) => {
        this.rationaleList = res.responseData.data;
        console.log(this.rationaleList);
      }
    )
  }

  selectRationale(data): void {
    let list = [];
    if (this.data.applicationStage === 's1') {
      this.applicationRationaleList.typeOfRationale = data;
      this.selectedRationaleList.push(this.applicationRationaleList);
      this.applicationRationaleList = new ApplicationRationaleList();
    } else {
      this.applicationRationaleList.typeOfRationale = data;
      this.selectedRationaleList.push(this.applicationRationaleList);
      this.applicationRationaleList = new ApplicationRationaleList();
    }
    console.log(this.selectedRationaleList);




  }

  removeRationale(id) {
    const index: number = this.selectedRationaleList.findIndex(srl => srl.id === id);
    this.selectedRationaleList.splice(index, 1);
  }

  submitRationale(): void {
    let endpoint;
    let swalMessage;
    let activeModel;

    this.spinner.show();

    if (this.data.applicationStage === 's1') {
      endpoint = ENDPOINTS.applicationRationale;
      swalMessage = 'S1 application rationale has been saved!'
    } else {
      endpoint = ENDPOINTS.applicationRationaleS2;
      swalMessage = 'S2 application rationale has been saved!';
      this.rationale.id = 0;
    }

    this.rationale.applicationRationaleLists = this.selectedRationaleList;
    this.rationale.actionDate = new Date();
    this.rationale.actionBy = this.currentUser;
    this.rationale.others = this.otherDescription;


    console.log('save rationale', this.rationale);

    this.apiService.save(endpoint, this.rationale).subscribe(
      (res: any) => {
        this.spinner.hide();
        this.sweetAlertService.customSuccessMessage(swalMessage);
        this.dialogRef.close(res);
      }, (err: any) => {
        console.log(err);
      }
    )
  }

  cancel(): void {
    this.dialogRef.close();
  }

}
