import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Affiliation, AscRate, Brand, Category, PaymentType, Product, TypeOfApplication, TypeOfDocument, TypeOfMedium, TypeOfReason } from '@models';
import { Announcement } from 'app/core/models/announcement.model';
import { AscRateSpecial } from 'app/core/models/asc-rate-special.model';
import { BrandPenalties } from 'app/core/models/brand-penalties.model';
import { CompanyPenalties } from 'app/core/models/company-penalties.model';
import { Dialect } from 'app/core/models/dialect.model';
import { EmailNotification } from 'app/core/models/email-notification.model';
import { Holiday } from 'app/core/models/holiday.model';
import { LoginImage } from 'app/core/models/login-image.model';
import { MediumExecutionController } from 'app/core/models/medium-execution-controller.model';
import { NumberOfMultipleApplicationAllowed } from 'app/core/models/numberOfMultipleApplicationAllowed.model';
import { PromissoryNotePeriod } from 'app/core/models/promissory-note-period.model';
import { QrAndBarCodeChecker } from 'app/core/models/qrAndBarCodeChecker.model';
import { S1AndS2FormExpirationPeriod } from 'app/core/models/s1-and-s2-form-expiration-period.model';
import { ScreenerReviewerPair } from 'app/core/models/screener-reviewer-pair.model';
import { TypeOfMainDocument } from 'app/core/models/type-of-main-document.model';
import { TypeOfMediumExecution } from 'app/core/models/type-of-medium-execution.model';
import { TypeOfRational } from 'app/core/models/type-of-rational.model';
import { UploadingDocumentFileSize } from 'app/core/models/uploading-document-file-size-model';

@Component({
  selector: 'app-form-action-buttons',
  templateUrl: './form-action-buttons.component.html',
  styleUrls: ['./form-action-buttons.component.scss']
})
export class FormActionButtonsComponent implements OnInit {

  @Input() isFromModal = false;
  @Input() showApproveRejectButton = false;

  @Input() childObject: ChildObject;
  @Input() childForm: FormGroup;
  @Output() saveFunction: EventEmitter<any> = new EventEmitter();
  @Output() validateFunction: EventEmitter<any> = new EventEmitter();
  @Output() updateFunction: EventEmitter<any> = new EventEmitter();
  @Output() deleteFunction: EventEmitter<any> = new EventEmitter();
  @Output() cancelFunction: EventEmitter<any> = new EventEmitter();
  @Output() closeModalFunction: EventEmitter<any> = new EventEmitter();
  @Output() approveFunction: EventEmitter<any> = new EventEmitter();
  @Output() rejectFunction: EventEmitter<any> = new EventEmitter();

  @Input() isHideSaveButton = false;
  @Input() isShowApproveDisapproveButton = false;
  isHideDeleteButton = false;
  isHideUpdateButton = false;
  isShowValidateButton = false;

  constructor() { }

  ngOnInit(): void {
    // hides the save button if class of childObject is email notification
    this.isHideSaveButton = this.childObject instanceof EmailNotification
      || this.childObject instanceof UploadingDocumentFileSize
      || this.childObject instanceof QrAndBarCodeChecker
      || this.childObject instanceof NumberOfMultipleApplicationAllowed
      || this.childObject instanceof AscRateSpecial;
    this.isHideUpdateButton = this.childObject instanceof LoginImage;
    this.isHideDeleteButton = this.childObject instanceof Category
      || this.childObject instanceof Product
      || this.childObject instanceof Brand
      || this.childObject instanceof BrandPenalties
      || this.childObject instanceof CompanyPenalties
      || this.childObject instanceof Affiliation
      || this.childObject instanceof TypeOfMedium
      || this.childObject instanceof TypeOfMediumExecution
      || this.childObject instanceof MediumExecutionController
      || this.childObject instanceof TypeOfReason
      || this.childObject instanceof TypeOfDocument
      || this.childObject instanceof PaymentType
      || this.childObject instanceof Holiday
      || this.childObject instanceof Dialect
      || this.childObject instanceof ScreenerReviewerPair
      || this.childObject instanceof EmailNotification
      || this.childObject instanceof TypeOfMainDocument
      || this.childObject instanceof Announcement
      || this.childObject instanceof S1AndS2FormExpirationPeriod
      || this.childObject instanceof PromissoryNotePeriod
      || this.childObject instanceof AscRate
      || this.childObject instanceof TypeOfApplication
      || this.childObject instanceof TypeOfRational
      || this.childObject instanceof LoginImage;
    this.isShowValidateButton = this.childObject instanceof QrAndBarCodeChecker;
  }

  save() {
    this.saveFunction.emit(this.childObject);
  }

  validate() {
    this.validateFunction.emit(this.childObject);
  }

  update() {
    this.updateFunction.emit(this.childObject);
  }

  delete() {
    this.deleteFunction.emit(this.childObject);
  }

  approve() {
    this.approveFunction.emit();
  }

  reject() {
    this.rejectFunction.emit();
  }

  closeModal() {
    this.closeModalFunction.emit();
  }

  resetFormValidator() {
    this.cancelFunction.emit();
  }

}

interface ChildObject {
  id: number;
  reason: string;
  typeOfMedium: string;
  paymentDescription: string;
  description: string;
  fileSize: number;
  clearingReleaseTime: string;
  decisionReleaseTime: string;
  sequenceNo: number;
  status: string;
  submissionEndTime: string;
  submissionStartTime: string;
}
