import { Component, ElementRef, Inject, OnInit, Type, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { S1Application, S2Application, TypeOfReason } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { APPLICATION_STATUS, APPLICATION_STATUS_S2, APP_DECISION_STATUS, COMMON_FUNCTION, ENDPOINTS, SUBMISSION_TYPE } from '@shared';
import { ApplicationRejection } from 'app/core/models/application-rejection.model';
import { NgxSpinnerService } from 'ngx-spinner';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-reject-disapproved-reason',
  templateUrl: './reject-disapproved-reason.component.html',
  styleUrls: ['./reject-disapproved-reason.component.scss']
})
export class RejectDisapprovedReasonComponent implements OnInit {

  @ViewChild('selectReason')
  mySelectReason: ElementRef;
  s1Application = new S1Application();
  s2Application = new S2Application();
  applicationRejection = new ApplicationRejection();
  typeOfReason = new TypeOfReason();
  applicationRejectionList: Array<ApplicationRejection> = [];
  typeOfReasonList = [];
  selectedReasonList = [];
  errMsg: any;

  headerTitle;
  ascUser;
  action;
  status;
  currentUser: any;

  remarks: string;


  constructor(
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private router: Router,
    private dataStorage: DataStorageService,
    private spinner: NgxSpinnerService,
    public dialogRef: MatDialogRef<RejectDisapprovedReasonComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit(): void {
    this.getTypeOfReasonsList();

    this.s1Application = this.data.s1Application;
    this.s2Application = this.data.s2Application;
    this.ascUser = this.data.ascUser;
    this.action = this.data.action;
    this.status = this.data.status;
    this.headerTitle = this.action;
    this.currentUser = this.dataStorage.getUserAccount();
  }

  getTypeOfReasonsList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfReason).subscribe(
      (res: any) => {
        this.typeOfReasonList = res.responseData.data;
        console.log('reasons => ', this.typeOfReasonList)
      }
    )
  }

  setRejectionData(): void {
    let endpoint
    if (this.data.applicationStage === 's1') {
      this.applicationRejection.applicationForm = this.s1Application;
      endpoint = ENDPOINTS.s1ReasonOfRejection;
    } else {
      this.applicationRejection.applicationS2 = this.s2Application;
      endpoint = ENDPOINTS.s2ReasonOfRejection;
    }
    this.applicationRejection.actionBy = this.currentUser;
    this.applicationRejection.actionDate = new Date();
    this.applicationRejection.typeOfReason = this.typeOfReason;
    this.applicationRejectionList.push(this.applicationRejection);
    this.applicationRejection = new ApplicationRejection();
  }

  selectedReason(data) {
    const isExist = this.selectedReasonList.filter(reason => reason.id === data.id);

    if (isExist.length === 0) {
      this.selectedReasonList.push(data);
      this.typeOfReason = data;
      this.setRejectionData();
      this.errMsg = '';
    } else {
      this.errMsg = 'The selected reason is already added.';
    }
    this.mySelectReason.nativeElement = '';
    
  }

  checkOthersReason(): boolean{
    return this.selectedReasonList.filter(e=> e.id ===4).length? true : false;
  }

  removeReason(id) {
    const index: number = this.selectedReasonList.findIndex(srl => srl.id === id);
    this.selectedReasonList.splice(index, 1);
  }

  cancel(): void {
    this.dialogRef.close();
  }

  submitRejectionDisapproved(): void {
    // console.log('PARAMETER', this.ascUser, this.action, this.status);

    let msg;
    let endpointReason;
    let endpointReject;
    let swalMsg = 'Application has been rejected/disapproved successfully.';
    let applicationForm: any;
    let navigateToList;

    if (this.status === APPLICATION_STATUS.S1_APPLICATION_REJECTED || this.status === APPLICATION_STATUS_S2.S2_APPLICATION_REJECTED) {
      msg = COMMON_FUNCTION.REJECT;
      swalMsg = 'Application has been rejected successfully.';
    } else if (this.status === APPLICATION_STATUS.S1_APPLICATION_DISAPPROVED ||
      this.status === APPLICATION_STATUS_S2.S2_APPLICATION_DISAPPROVED) {
      msg = COMMON_FUNCTION.DISAPPROVED;
      swalMsg = 'Application has been disapproved.';
    }

    Swal.fire({
      text: 'Are you sure you want to ' + msg + ' this application?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        if (this.data.applicationStage === 's1') {
          applicationForm = this.s1Application;
          navigateToList = 'asc/page/application/s1/lists';
          endpointReason = ENDPOINTS.s1ReasonOfRejection;
          endpointReject = this.s1Application.submissionType === SUBMISSION_TYPE.ORIGINAL
            ? this.s1Application.decisionStatus === APP_DECISION_STATUS.INCOMPLETE
              ? ENDPOINTS.s1ApplicationFormEncoderRejectCompliance : ENDPOINTS.s1ApplicationFormEncoderReject
            : ENDPOINTS.s1ApplicationFormEncoderRejectRevision;
        } else {
          applicationForm = this.s2Application;
          navigateToList = 'asc/page/application/s2/lists';
          endpointReason = ENDPOINTS.s2ReasonOfRejection;
          endpointReject = ENDPOINTS.s2ApplicationFormEncoderReject;
        }
        console.info('apllicationRejectionList => ', this.applicationRejectionList)
        // this.spinner.hide();
        this.applicationRejectionList.filter( e=>{
          if(e.typeOfReason.id === 4){
            e.remarks = this.remarks
          }
        });
        this.apiService.save(endpointReason, this.applicationRejectionList).subscribe(
          (res: any) => {
            this.apiService.save(endpointReject, applicationForm).subscribe(
              (res1: any) => {
                this.s1Application = res1.responseData.data;
                // Forward Multiple Application
                if (this.s1Application.submissionType === SUBMISSION_TYPE.ORIGINAL &&
                  this.data.applicationStage === 's1' && this.s1Application.multipleApplication &&
                  this.s1Application.multipleApplication.actualNumberOfApplicationForm ===
                  this.s1Application.multipleApplication.countVerifiedApplicationForm &&
                  this.s1Application.internalStatus !== APPLICATION_STATUS.S1_APPLICATION_REVIEWED) {
                  this.forwardMultipleAppsTo();
                } else {
                  this.spinner.hide();
                  this.sweetAlertService.customSuccessMessage(swalMsg);
                  this.dialogRef.close();
                  this.router.navigateByUrl(navigateToList);
                }
              })
          })
      }
    })
  }


  forwardMultipleAppsTo() {
    console.log('forwardMultipleAppsTo');
    console.log(this.s1Application);
    this.s1Application.multipleApplication.assignedEncoder = this.s1Application.assignedEncoder;
    this.apiService.save(ENDPOINTS.forwardMultipleAppFormToAdSpe, this.s1Application.multipleApplication).subscribe(
      (res: any) => {
        console.log(res);
        this.spinner.hide();
        this.sweetAlertService.customSuccessMessage('Multiple application successfully forwarded to the next user.');
        this.dialogRef.close();
        this.router.navigateByUrl('asc/page/application/s1/lists');
      }, (err: any) => {
        this.spinner.hide();
        this.sweetAlertService.error(err);
      }, () => {
        this.spinner.hide();
      })
  }


}
